<template>
  <div class="charge-container">
    <!-- 面包屑 -->
    <div class="breadcrumb">
      <span class="breadcrumb-item">
        <span class="breadcrumb-item-inner" @click="toFinance()">财务管理</span>
        <span class="breadcrumb-separator">
          <img src="//assets.qkcdn.com/images/13ace5534e3236037023c60549d43d80.png">
        </span>
      </span>
      <span class="breadcrumb-item">
        <span class="breadcrumb-item-inner">{{currentPageTitle}}</span>
      </span>
    </div>

    <div v-show="activeTabName === 'chinabank'" class="step step1">第一步：在线提交付款信息</div>
    <el-form v-show="activeTabName === 'chinabank'" ref="bankform" :model="info" :rules="rules">

      <article class="flex-inline">
        <label>结算金额</label>
        <el-input placeholder="请输入结算金额" class="small" v-model="info.amount"></el-input>
      </article>

      <article class="flex-inline">
        <label>确认金额</label>
        <el-input placeholder="请再次输入结算金额" class="small" v-model="info.amount_check"></el-input>
      </article>

      <article class="flex-inline">
        <label>付款人</label>
        <el-input placeholder="请输入付款人/公司名称" class="small" v-model="info.refer_drawee"></el-input>
      </article>

      <article style="margin-top: 30px;">
        <label style="display:block; float: left; width: 83px; line-height: 40px; font-size: 13px;">是否开票</label>
        <el-radio-group v-model="info.invoice_status">
          <el-radio-button class="el-icon-check" label="0">不需要</el-radio-button>
          <el-radio-button class="el-icon-check" label="1">需要</el-radio-button>
        </el-radio-group>
      </article>

      <!-- 发票 -->
      <div v-if="info.invoice_status == 1">

        <article class="flex-inline">
          <label>发票抬头</label>
          <el-input placeholder="请输入发票抬头" class="large" v-model="info.invoice_title"></el-input>
        </article>

        <article class="flex-inline">
          <label>收件人</label>
          <el-input placeholder="请输入收件人" class="large" v-model="info.invoice_contact_name"></el-input>
        </article>

        <article class="flex-inline">
          <label>联系电话</label>
          <el-input placeholder="请输入联系电话" class="large" v-model="info.invoice_contact_phone"></el-input>
        </article>

        <article class="flex-inline">
          <label>快递地址</label>
          <el-input placeholder="请输入快递地址" class="large" v-model="info.invoice_contact_address"></el-input>
        </article>

      </div>
      <!-- 发票 -->
  
      <article class="flex-inline">
        <label>备注信息</label>
        <el-input placeholder="请输入备注信息（选填）" class="large" v-model="info.remarks"></el-input>
      </article>

    </el-form>

    <div v-show="activeTabName === 'chinabank'" class="step step2">第二步：请将付款款项转入以下官方账户</div>
    <div v-show="activeTabName === 'chinabank'" style="margin-bottom:40px;" class="qk-account-info">

      <section>
        <dl>
          <dt>公司名称</dt>
          <dd>宁波钱咖广告传媒有限公司</dd>
        </dl>
        <dl>
          <dt>银行帐号</dt>
          <dd>3166 3803 0026 61224</dd>
        </dl>
        <dl>
          <dt>开户银行</dt>
          <dd>上海银行白玉兰支行</dd>
        </dl>
      </section>

    </div>

    <div>
      <el-button size="medium">取消</el-button>
      <el-button class="submitForm_btn" type="primary" size="medium" @click="submitForm()">提交</el-button>
    </div>

    <el-dialog class="bank-charge-success-dialog" :visible.sync="dialogVisible">
      <div slot="title">
        <div class="l-wrap"><img src="//qianka.b0.upaiyun.com/images/425ec42718c6ef5cbe6e6fe998b66d12.png"/></div>
        <div class="r-wrap"><span class="fs16-c28">提交成功！</span>
          <div class="fs12-c88">我们会在核实银行转账信息后为您充值指定金额</div></div>
      </div>
      <div class="company-desc">公司名称：宁波钱咖广告传媒有限公司
        银行账户：3166 3803 0026 61224<br>
        开户银行：上海银行白玉支行</div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="backTo('.')">确认</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<style lang="scss">
  .charge-container {
    padding: 50px 0 150px 35px;

    .step{
      color: #888;
      &.step1{
        margin:51px 0 32px 0;
      }
      &.step2{
        margin:62px 0 32px 0;
      }
    }

    .breadcrumb{
      margin-top: 2px;
      color: #888;
      .breadcrumb-separator{
        img{
          height: 12px; margin: 0 5px 0 6px;
        }
      }
    }

    .flex-inline{
      display: flex; align-items: center; margin-top: 30px;
      label{
        width: 83px; font-size: 13px;
      }
    }

    .qk-account-info{
      width: 368px; border: 1px solid #e8e8e8; font-size: 13px;
      dl{
        clear: both; height: 46px; line-height: 46px; margin: 0;
        dt{
          display: block; float: left; width: 131px; background-color: #f9f9f9; text-align: center; border-bottom: 1px solid #e8e8e8;
        }
        dd{
          display: block; float: left; width: 235px; margin: 0; padding: 0; text-align: center; border-bottom: 1px solid #e8e8e8;
        }
      }
    }

    .submitForm_btn{
      margin-left: 16px;
    }
  }
</style>
<script>
  import {mapState, mapActions} from 'vuex'
  import {Message} from 'element-ui'
  import qs from 'qs'
  import _ from 'lodash'

  export default {
    data () {
      return {
        isNeedInvoice: false,
        activeTabName: 'chinabank',
        dialogVisible: false,
        alipayDialogVisible: false
      }
    },

    computed: {
      currentPageTitle () {
        return this.$router.currentRoute.name === 'credit_dash_finance_charge' ? '结算' : '编辑'
      },

      rules () {
        let checkCoast = (rule, value, callback) => {
          if (!value) {
            return callback(new Error('充值金额不能为空'))
          }
          setTimeout(() => {
            if (!/(?=^[.\d]+$)(?=^.*\.?)(?!^\d*\.\d*\.)[.\d]*$/.test(value)) {
              callback(new Error('请输入有效金额格式'))
            }
            if (value < 0) {
              callback(new Error('金额必须大于0'))
            } else {
              callback()
            }
          }, 1000)
        }

        let checkCoastConfirm = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请再次输入金额'))
          } else if (value !== this.info.amount) {
            callback(new Error('两次输入金额不一致!'))
          } else {
            callback()
          }
        }

        let checkPhone = (rule, value, callback) => {
          if (!/^1[3|4|5|8]\d{9}$/.test(value)) {
            callback(new Error('手机号格式不正确'))
          } else {
            callback()
          }
        }

        let rules = {
          amount: [
            { required: true, validator: checkCoast, trigger: 'blur' }
          ],
          amount_check: [
            { required: true, validator: checkCoastConfirm, trigger: 'blur' }
          ],
          refer_drawee: [
            { required: true, message: '请输入付款人名称', trigger: 'change' }
          ]
        }

        let rulesAdded = {...rules,
          invoice_title: [{required: true, message: '请输入发票抬头', trigger: 'blur'}],
          invoice_contact_name: [{required: true, message: '请输入收件人', trigger: 'blur'}],
          invoice_contact_phone: [{required: true, validator: checkPhone, trigger: 'blur'}],
          invoice_contact_address: [{required: true, message: '请输入快递地址', trigger: 'blur'}]
        }

        return this.invoice_status === 0 ? rules : rulesAdded
      },

      alirules () {
        let checkCoast = (rule, value, callback) => {
          if (!value) {
            return callback(new Error('充值金额不能为空'))
          }
          setTimeout(() => {
            if (!/(?=^[.\d]+$)(?=^.*\.?)(?!^\d*\.\d*\.)[.\d]*$/.test(value)) {
              callback(new Error('请输入有效金额格式'))
            }
            if (value < 1000) {
              callback(new Error('最小充值金额1000元'))
            } else if (value > 99999999) {
              callback(new Error('最大充值金额99999999元'))
            } else {
              callback()
            }
          }, 1000)
        }

        let checkPhone = (rule, value, callback) => {
          if (!/^1[3|4|5|8]\d{9}$/.test(value)) {
            callback(new Error('手机号格式不正确'))
          } else {
            callback()
          }
        }

        let aliRules = {
          // ali_drawee: [{ required: true, message: '请输入付款人的支付宝账号', trigger: 'change' }],
          ali_amount: [{ required: true, validator: checkCoast, trigger: 'blur' }]
        }

        let alirulesAdded = {...aliRules,
          invoice_title: [{required: true, message: '请输入发票抬头', trigger: 'blur'}],
          invoice_contact_name: [{required: true, message: '请输入收件人', trigger: 'blur'}],
          invoice_contact_phone: [{required: true, validator: checkPhone, trigger: 'blur'}],
          invoice_contact_address: [{required: true, message: '请输入快递地址', trigger: 'blur'}]
        }

        return this.invoice_status === 0 ? aliRules : alirulesAdded
      },

      ...mapState('loanCharge', [
        'info',
        'aliInfo'
      ])
    },

    fetchAction: 'loanCharge/getInvoiceInfo',

    methods: {
      toFinance () {
        this.$router.push('/d/credit/finance')
      },
      submitForm () {
        if (this.activeTabName === 'chinabank') {
          this.$refs['bankform'].validate((valid) => {
            if (valid) {
              this.submitInfo(this.info).then((res) => {
                this.dialogVisible = true
              }).catch((err) => {
                Message({
                  message: err.err_msg,
                  iconClass: 'qk-warning'
                })
              })
            }
          })
        } else {
          this.$refs['alipayform'].validate((valid) => {
            if (valid) {
              this.alipayDialogVisible = true
              var info = this.aliInfo
              var postInfo = {}
              _.each(info, function (val, key) {
                postInfo[key] = encodeURI(val)
              })
              let params = qs.stringify(postInfo)
              let url = '//' + location.host + '/v2/api/alipay?' + params
              window.open(url)
            }
          })
        }
      },

      backTo () {
        setTimeout(() => {
          this.$router.push('/d/credit/finance')
        })
      },

      ...mapActions('creditCharge', [
        'getInvoiceInfo',
        'submitInfo'
      ])
    }
  }
</script>



